.sidebar {
  background-color: #060b26;
  height: 100px;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: row-reverse;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
  z-index: 1;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.nav-menu.active span {
  margin-left: 16px;
  white-space: nowrap;
}
.nav-menu span {
  margin-left: 16px;
}

.logo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 3rem;
}

.logoDiv {
  display: flex;
  width: 91%;
  justify-content: center;
}

.welcome {
  text-align: center;
  padding: 10px;
  width: 500px;
  margin: 10px;
  border: 5px solid tan;
  max-height: 250px;
  border-radius: 10px;
  color: black;
}
